.modal-suc {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 1000;
}
.modal-suc.act {
    opacity: 1;
    pointer-events: all;

}
.content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
}